import React from "react";
import "./style/store_btn.css";
export default function StoreBtn(props) {
  return (
    <div className="container_btn">
      <img className="icon_store" alt="icon store" src={props.img} />
      <a href={props.link} className="link">
        <button className="button">
          <span>Descargar</span>
        </button>
      </a>
    </div>
  );
}
