import React from "react";
import "./style/service_card.css";
export default function ServiceCard(props) {
  return (
    <div className="card">
      <div className="conte_img">
        <img
          className="card_img"
          src={props.img}
          alt={"Servicio" + props.name}
        />
      </div>
      <div className="card_body_s">
        <h1>{props.name}</h1>
        <p className="price"> {"$" + props.price}</p>
        {props.listCard.map((element, index) => {
          // console.log("hola");
          return <p key={index}>{element}</p>;
        })}
      </div>
      <a href={props.url}>
        <p>
          <button> Contratar.</button>
        </p>
      </a>
    </div>
  );
}
