import ztiluz from "../img/ztiluz.png";
import "./style/nav.css";
import React, { Component } from "react";

export default class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      classNav: "",
    };
    this.handleClickNavActive = this.handleClickNavActive.bind(this);
    this.handleClickNavClose = this.handleClickNavClose.bind(this);
  }
  handleClickNavActive() {
    if (this.state.classNav === "") {
      this.setState({ classNav: "nav_handle" });
    }
  }
  handleClickNavClose() {
    if ((this.state.classNav ==="nav_handle")) {
      this.setState({ classNav: "" });
    }
  }

  render() {
    return (
      <React.Fragment>
        <div id="mySidenav" className={"sidenav " + this.state.classNav}>
          <div className="container-btnClose">
            <span className="closebtn" onClick={this.handleClickNavClose}>
              {""}&times;
            </span>
          </div>
          <a className="list_item" href="#home">Inicio</a>
          <a className="list_item" href="#service">Servicios</a>
          <a  className="list_item"href="#about">Conocenos</a>
          <a  className="list_item"href="#contact">Contactanos</a>
        </div>
        <nav className="nav">
          <div className="nav_item_img">
            <img src={ztiluz} alt="Ztiluz" className="img_logo"  />
          </div>
          <div className="nav_item">
            <span className={"btn_nav"} onClick={this.handleClickNavActive}>
              {" "}
              &#9776;{" "}
            </span>
          </div>
        </nav>
      </React.Fragment>
    );
  }
}
